import View from '../../../framework/view/_abstract';
import dayjs from 'dayjs';
function getTemplate(event) {
    const { dateFrom } = event;
    const date = dayjs(dateFrom).format('MMM DD');
    return `<time class="event__date" datetime="${dateFrom}">${date}</time>`;
}
export default class Date extends View {
    event;
    constructor(event) {
        super();
        this.event = event;
    }
    get template() {
        return getTemplate(this.event);
    }
}
